
import 'common/css/reset.css';
import 'common/js/common.js';
import geturlpara from 'common/js/geturlpara.js';
import Vue from 'vue'
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import VueResource from 'vue-resource'
Vue.use(VueResource)
Vue.prototype.$geturlpara=geturlpara

export default {
  name: 'channelsdetail',
  data () {
    return {
      loadingshow: true,
      loadtext: 'loading...',
      channelsinfo:{

      },
      information:""
    }
  },
  components: {
    XHeader,
    Loading,
    Scroller
  },
  created () {
    var id = this.$geturlpara.getUrlKey("id")||"300";
    this.fetchData(id);
  },

  methods: {
    //获取专栏数据数据
    fetchData(id){
      this.$http.get('/api/channels/'+id+'.json', [])
      .then((data)=>{
        this.loadingshow=false;
        this.channelsinfo=JSON.parse(data.bodyText);
        //正则匹配，处理information
        this.information=this.channelsinfo.information.replace(/[。]/g,"。<br/>") ;
        var self=this;
        setTimeout(function () {
          self.$nextTick(() => {
            self.$refs.scrollerEvent.reset()
          })
        },100);
      }, (err)=>{console.log(err);});
    },
    share(){
      console.log("share");
    },
    geDetail(id){
      console.log(id);
    }
  }
}



